@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0098ca;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal-wrapper {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right:0;
    text-align: center;
}

.modal-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right:0;
    z-index: 100;
    background-color: rgba(0,0,0,0.3);
}

.modal-box {
    position: relative;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-height: 30%;
    width: 60%;
    background-color: #E9EDF5;
    box-shadow: 0 0 10px rgba(0,0,0,0.25);
    z-index: 101;
    padding: 40px;
    border-radius: 10px;
}
@media (max-width: 680px) {
    .modal-box {
        padding: 10px 0;
        width: 100%;
    }
}
@media (max-width: 320px) {
    .modal-box {
        padding: 10px 0;
        width: 100%;
        top: 60%;
    }
}

input {
    border: 1px solid #FF5E5B;
    padding: 0.5rem;
    width: 200px;
    border-radius: 30px;
}

.no-suggestions {
    color: #999;
    padding: 0.5rem;
}

.suggestions {
    border: 1px solid #999;
    border-top-width: 0;
    list-style: none;
    margin-top: 0;
    max-height: 143px;
    overflow-y: auto;
    padding-left: 0;
    width: calc(300px + 1rem);
}

.suggestions li {
    padding: 0.5rem;
}

.suggestion-active,
.suggestions li:hover {
    background-color: #FF5E5B;
    color: white;
    cursor: pointer;
    font-weight: 700;
}

.suggestions li:not(:last-of-type) {
    border-bottom: 1px solid #999;
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-text {
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

form > h4 {
  font-size: 1.2rem;
  margin-top: 1.7rem;
  margin-bottom: 1rem;
}

.cat-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cat {
  margin: 0.5rem;
  position: relative;
}

.cat-type {
  position: absolute;
  top: 10%;
  font-weight: 600;
  color: black;
  font-size: 1.7rem;
  z-index: 2;
  width: 100%;
  text-align: center;
}

.selected-filter {
  color: #FF5E5B !important;
}

.cat-img {
  width: 12rem;
  border-radius: 1rem;
  opacity: 0.6;
}

@media (max-width: 680px) {
  .cat-img {
    width: 8rem;
  }
  .cat-type{
    font-size: 1.3rem;
  }
}

.start-btn {
  display: inline-block;
  background-color: white;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 40px;
  border: none;
  border-radius: 30px;
  font-family: "Abel", sans-serif;
  font-size: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: #FF5E5B;
  box-shadow: 0 3px 0 #FF5E5B;
  cursor: pointer;
  transition: all 100ms linear;
}
.setting-btn:hover {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  transform: translateY(-4px);
  box-shadow: 0 7px 0 #FF5E5B;
}
.setting-btn:focus {
  outline: 0;
  
}
