    background-image: url(/img/travel.png);
    background-size: 100%;
    background-repeat:no-repeat;
    background-position: center;
    width: 100%;
    min-height: 100vh;
   display: flex;
   justify-content: center;
   align-content: center;
}

.landing-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    margin-top: 20%;
}

.memory-travel {
    display: flex;
    color: #be1010;
    font-size: 5rem;
    font-family: 'Righteous', cursive;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20%;
    
}

.button-enter {
